*{
    font-family: Arial, Helvetica, sans-serif;
}

body{
    padding: 60px;

}

input {
    color: grey;
    width: 100%;
    border-radius: 10px;
    font-size: 1.3rem;
}
h2{
    text-align: center;
}


.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 10px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;

}

.card img{
    max-height: 170px;

}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}